				"html": "<h1 id=\"transforming-code\"><a href=\"#transforming-code\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Transforming Code</h1>\n<p>Much of programming is simply mapping one kind of code to another. For example, you have to get your forms lined up with your state management system and your networking code to be in sync with your API.</p>\n<p>Optic automates these mappings with our Transformations which appear in the Search Panel whenever they are available for the code you are interacting with. Transformations take one code object and return another of a known type. </p>\n<p>Here’s an example where we convert a Mongoose Model into a Create Route using Express JS. </p>\n<p><img src=\"/doc-images/transform.gif\" alt=\"alt text\"></p>",
